@media screen and (max-width: 767px) {
  .menu, .header-address {
    display: none;
  }

  .mobile-menu-open {
    stroke: var(--secondary-bg-color);
    background-color: rgba(0, 0, 0, 0);
    border: none;
    padding: 0;
    line-height: 0;
  }

  .mobile-menu {
    width: 100%;
    height: 100%;
    z-index: 999;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    background-color: var(--primary-bg-color);
    position: fixed;
    top: 0;
    left: 0;
  }

  .mobile-menu.is-open {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
  }

  .mobile-menu-container {
    height: 100%;
    flex-direction: column;
    justify-content: space-between;
    padding: 80px 40px 40px 35px;
    display: flex;
    position: relative;
  }

  .modal-close-btn {
    width: 24px;
    height: 24px;
    background: var(--decoration-color);
    border: 1px solid rgba(0, 0, 0, .1);
    border-radius: 50%;
    justify-content: center;
    align-items: center;
    padding: 0;
    transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
    display: flex;
    position: absolute;
    top: 24px;
    right: 15px;
  }

  .modal-close-btn:hover, .modal-close-btn:focus {
    background-color: var(--accent-color);
  }

  .modal-close-btn:hover .modal-close-icon, .modal-close-btn:focus .modal-close-icon {
    fill: var(--secondary-text-color-theme-dark);
  }

  .mobile-menu-item:not(:last-child) {
    margin-bottom: 40px;
  }

  .mobile-menu-link {
    letter-spacing: .02em;
    color: var(--secondary-text-color);
    font-size: 36px;
    font-weight: 700;
    line-height: 1.11;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
    display: block;
  }

  .current-page, .mobile-menu-link:hover, .mobile-menu-link:focus {
    color: var(--accent-color);
  }

  .mobile-list-phone-item {
    margin-bottom: 40px;
  }

  .mobile-list-phone-link {
    letter-spacing: .02em;
    text-transform: capitalize;
    color: var(--primary-logo-text-color);
    font-size: 36px;
    font-weight: 700;
    line-height: 1.11;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .mobile-list-phone-link:hover, .mobile-list-phone-link:focus {
    color: var(--accent-color);
  }

  .mobile-list-mail-item {
    margin-bottom: 48px;
  }

  .mobile-list-mail-link {
    letter-spacing: .02em;
    color: var(--primary-text-color);
    font-size: 20px;
    font-weight: 500;
    line-height: 1.2;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .mobile-list-mail-link:hover, .mobile-list-mail-link:focus {
    color: var(--accent-color);
  }

  .mobile-social-list {
    justify-content: space-between;
  }
}

@media screen and (max-width: 427px) {
