    font-size: 12px;
    font-style: normal;
    line-height: 1.17px;
    transition: color .25s cubic-bezier(.4, 0, .2, 1);
  }

  .contact-list-link:hover, .contact-list-link:focus {
    color: var(--accent-color);
  }

  .current {
    color: var(--accent-color);
    position: relative;
  }

  .current:after {
    content: "";
    width: 100%;
    height: 4px;
    background-color: var(--accent-color);
    border-radius: 2px;
    display: inline-block;
    position: absolute;
    bottom: 0;
    left: 0;
    transform: translate(0, 21px);
  }
}

@media screen and (min-width: 1200px) {
  .page-header {
    padding-bottom: 0;
  }

  .logo-link {
    line-height: 1.33;
  }

  .contact-list {
    display: flex;
  }

  .contact-list-item:not(:first-child) {
    margin-left: 40px;
  }

  .contact-list-link {
    letter-spacing: .02em;
    font-size: 16px;
    line-height: 1.5;
  }

  .current:after {
    content: "";
    transform: translate(0, 31px);
  }
}

